<template>
<main>
    <ImageModal v-if="modalVisible" @close="modalVisible = false" :imagedata="modalData"/>
    <Header :header="content"/>
    <h5 style="text-align:center;color:green;"> برای مثال کد 12345 وارد شود</h5>
    <div>
    <p>
        <input type="text" v-model="barcode" placeholder="کد پرسنلی مورد نظر را وارد نمایید ...">
        <button @click="getPart" class="btn btn-primary d-block mx-auto">دریافت</button>
    </p>
    </div>
    
    <h4 style="text-align:center;color:red;">{{error}}</h4>
    <table class="table   table-hover container" >
            <tbody>
                <tr>
                <th>نام</th>
                <td>{{name}}</td>
                </tr>

                <tr>
                <th>نام خانوادگی</th>
                <td>{{lastname}}</td>
                </tr>

                <tr>
                <th>کد پرسنلی</th>
                <td>{{code}}</td>
                </tr>

                <tr>
                <th>سمت</th>
                <td>{{who}}</td>
                </tr>

                <tr>
                <th>قسمت</th>
                <td>{{div}}</td>
                </tr>

                <tr>
                <th>بخش</th>
                <td>{{section}}</td>
                </tr>


                <tr>
                <th>عکس کاربر</th>
                <td><button     v-if="statuscode==200"   class="btn btn-success" @click="openModal()">نمایش</button></td>
                </tr>

                

            </tbody>
    </table>
</main>
</template>

<script>
import axios from 'axios'
import Header from '../../components/Header.vue'
import ImageModal from '../../components/ImageModal.vue'
export default {
    name:'PersonnelView',
    components:{Header,ImageModal},
    data() {
        return {
            modalVisible: false,
            modalData: null,
            content:'اطلاعات پرسنل',
            name:'',
            lastnamr:'',
            code:'',
            who:'',
            div:'',
            section:'',
            error:'',
            barcode:'',
            pic:'',
            statuscode:''
        }
    },
    methods: {
        getPart(){
            if(this.barcode==='12345'){
            axios.get('../personnel.json')
            .then((response)=>{
                this.name=response.data.name;
                this.lastname=response.data.lastname;
                this.code=response.data.code;
                this.who=response.data.who;
                this.div=response.data.div;
                this.section=response.data.section;
                this.modalData=response.data;
                this.barcode='';
                this.statuscode=response.status;
               
                })
            
            }else{
                this.error='کدپرسنلی اشتباه وارد شده است';
                this.barcode='';
            }
        },

        openModal() {
            
            this.modalVisible = true;
        }
    },
}
</script>

<style lang="scss" scoped>
main{

    div{
        text-align: center;
            p{
                position: relative;
                display: inline-block;
                input{
                    
                    position:relative;
                    width: 30vw;
                    
                }
                button{
                    position: absolute;
                    left: 0;
                    top: 0;
                    bottom: 0;
                }

            }      

    }

    table{
        border-radius: 2px;
        background-color: rgb(255, 255, 255);
        width:80%;
        th{
            width: 15%;
        }
        td{
            text-align: center;
        }
    }
    
        

}
</style>